export default {
  data() {
    return {
      content: ''
    }
  },
  created() {
  },
  methods: {
    clickVisiteWrapper() {
      this.$emit('clickWrapper')
    },
    clickContent(event) {
      event.stopImmediatePropagation()
    },
    sendInvite () {
      this.$emit('invited')
    }
  }
}



